.dashboard__container {
	display: flex;
	flex-direction: row;
}

.dashboard__aside {
	width: 250px;
	min-width: 250px;
	height: 100vh;
	background-color: #191a19;
	font-family: "Red Hat Mono", monospace;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.dashboard__title {
	text-align: center;
	padding: 2em 0.5em;
}
.dashboard__title h2 span {
	color: #4e9f3d;
}
.dashboard__title p {
	color: white;
	font-size: x-small;
}
.dashboard__main {
	width: 100%;
	height: 100vh;
	background-color: #2b2c2b;
}
